.page-header {
    background: #1A1A1A;
    
    position: sticky;
    top: 0;
    z-index: 15;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
    .btn-disclaimer{
        font-size: 10px;
        padding-top: 3px;
        color: #fff;
    }
}

.logo {
    display: inline-block;
    img{
        display: block;
        max-width: 100%;
        &.m-mobile{
            display: none;
        }
    }
}

.page-header__wrap {
    padding: 8px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.page-header__info {
    font-size: 24px;
    line-height: 1.2;
    font-family: 'montsemibold';
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    span {
        color: #FF6633;
        display: block;
    }
}

.top-btn {
    display: inline-block;
    background: #f63;
    border: 2px solid #f63;
    border-radius: 100px;
    color: #fff;
    text-decoration: none;
    padding: 7px 21px;
    font-size: 18px;
    text-transform: uppercase;
    font-family: montsemibold;
    transition: all .25s ease;
    min-width: 235px;
    text-align: center;
    &:hover{
        color: #fff;
        border: 2px solid #f45520;
        background: #f45520;
    }
}
