//MEDIA
@media screen and (max-width: 991px){
    h1{
        font-size: 48px;
    }
    h3{
        font-size: 24px;
    }
}
@media screen and (max-width: 767px) {
    body{
        font-size: 14px;
    }
    h1{
        font-size: 32px;
        margin-bottom: 20px;
    }
    h2{
        font-size: 24px;
        margin-bottom: 30px;
    }
    h3{
        font-size: 20px;
        margin-bottom: 20px;
    }
    h4{
        font-size: 18px;
    }
    .logo img{
        
    }
    
    .btn {
        padding: 12.5px 20px;
        font-size: 18px;
    }
    .page-header__info{
        display: none;
    }
    .page-footer{
        padding: 20px 0;
        font-size: 12px;
        p{
            margin: 0 0 15px 0;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    .page-top {
        padding: 40px 0;
    }
    .page-footer {
        padding: 0 0 40px;
        font-size: 12px;
    }
    .page-footer__wrap {
        flex-wrap: wrap;
        padding-top: 40px;
        padding-bottom: 45px;
        position: relative;
    }
    .page-footer__l {
        margin-right: 0;
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 30px;
    }
    .page-footer__info {
        text-align: center;
    }
}


@media screen and (max-width: 600px){
    input[type="text"]:-webkit-autofill {
        font-size: 12px!important;
    }
    input[type="text"]:-webkit-autofill,
    input[type="text"]:-webkit-autofill:hover,
    input[type="text"]:-webkit-autofill:focus,
    input[type="text"]:-webkit-autofill:active {
        font-size: 12px!important;
    }
    input:-webkit-autofill::first-line {
        font-size: 12px!important;
    }
    .p-disclaimer{
        font-size: 10px;
        line-height: 12px;
    }
    .page-header__wrap {
        justify-content: center;
        padding: 5px 0;
    }
}