html {
  height: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: "montregular", sans-serif;
  background: #fff;
  font-size: 18px;
  color: #1A1A1A;
  line-height: 1.3;
  overflow-x: hidden;
}
body.m-hidden {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

:-moz-any-link:focus {
  outline: 1px dotted invert;
}

:-moz-any-link:focus {
  outline: none;
}

textarea,
input {
  box-shadow: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

::-webkit-input-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

::-moz-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

:-moz-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

:-ms-input-placeholder {
  color: #888;
  transition: all 0.3s ease;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

input:required {
  -webkit-box-shadow: none;
  box-shadow: none;
}

input[type=text]:-webkit-autofill {
  font-size: 18px !important;
}

input[type=text]:-webkit-autofill,
input[type=text]:-webkit-autofill:hover,
input[type=text]:-webkit-autofill:focus,
input[type=text]:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  font-size: 18px !important;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  box-shadow: 0 0 0px 1000px #fff inset;
}

input:-webkit-autofill::first-line {
  font-size: 18px !important;
}

input[type=submit] {
  border: 0;
}

button {
  border: 0;
  outline: none;
}

@-webkit-keyframes fadeEffect {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeEffect {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
h1 {
  font-size: 68px;
  font-family: "montbold", sans-serif;
  margin: 0 0 43px 0;
  line-height: 1.15;
}

h2 {
  font-size: 40px;
  font-family: "montsemibold", sans-serif;
  margin: 0 0 40px 0;
  line-height: 1.2;
}

h3 {
  font-size: 28px;
  font-family: "montsemibold", sans-serif;
  margin: 0 0 27px 0;
}

h4 {
  font-size: 22px;
  font-family: "montsemibold", sans-serif;
  margin: 0 0 32px 0;
}

strong {
  font-family: "montbold", sans-serif;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: flex;
  align-items: flex-start;
  list-style-type: none;
  line-height: 1.25;
  margin-bottom: 16px;
}
li:last-child {
  margin-bottom: 0;
}
li:before {
  content: "";
  width: 6px;
  min-width: 6px;
  height: 6px;
  background: #FF6633;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
  margin: 8px 18px 0 0;
}

@font-face {
  font-family: "montregular";
  src: url("../fonts/mont/mont-regular/mont-regular.eot");
  src: url("../fonts/mont/mont-regular/mont-regular.eot?#iefix") format("embedded-opentype"), url("../fonts/mont/mont-regular/mont-regular.woff2") format("woff2"), url("../fonts/mont/mont-regular/mont-regular.woff") format("woff"), url("../fonts/mont/mont-regular/mont-regular.ttf") format("truetype"), url("../fonts/mont/mont-regular/mont-regular.svg#montregular") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "montsemibold";
  src: url("../fonts/mont/mont-semibold/mont-semibold.eot");
  src: url("../fonts/mont/mont-semibold/mont-semibold.eot?#iefix") format("embedded-opentype"), url("../fonts/mont/mont-semibold/mont-semibold.woff2") format("woff2"), url("../fonts/mont/mont-semibold/mont-semibold.woff") format("woff"), url("../fonts/mont/mont-semibold/mont-semibold.ttf") format("truetype"), url("../fonts/mont/mont-semibold/mont-semibold.svg#montsemibold") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "montbold";
  src: url("../fonts/mont/mont-bold/mont-bold.eot");
  src: url("../fonts/mont/mont-bold/mont-bold.eot?#iefix") format("embedded-opentype"), url("../fonts/mont/mont-bold/mont-bold.woff2") format("woff2"), url("../fonts/mont/mont-bold/mont-bold.woff") format("woff"), url("../fonts/mont/mont-bold/mont-bold.ttf") format("truetype"), url("../fonts/mont/mont-bold/mont-bold.svg#montbold") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.layout {
  flex: 1 0 auto;
}

.container {
  width: 100%;
  max-width: 1216px;
  padding: 0 20px;
  position: relative;
  margin: 0 auto;
}

.page-content {
  padding: 20px 0;
}

.page-header {
  background: #1A1A1A;
  position: sticky;
  top: 0;
  z-index: 15;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
}
.page-header .btn-disclaimer {
  font-size: 10px;
  padding-top: 3px;
  color: #fff;
}

.logo {
  display: inline-block;
}
.logo img {
  display: block;
  max-width: 100%;
}
.logo img.m-mobile {
  display: none;
}

.page-header__wrap {
  padding: 8px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.page-header__info {
  font-size: 24px;
  line-height: 1.2;
  font-family: "montsemibold";
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.page-header__info span {
  color: #FF6633;
  display: block;
}

.top-btn {
  display: inline-block;
  background: #f63;
  border: 2px solid #f63;
  border-radius: 100px;
  color: #fff;
  text-decoration: none;
  padding: 7px 21px;
  font-size: 18px;
  text-transform: uppercase;
  font-family: montsemibold;
  transition: all 0.25s ease;
  min-width: 235px;
  text-align: center;
}
.top-btn:hover {
  color: #fff;
  border: 2px solid #f45520;
  background: #f45520;
}

.page-footer {
  padding: 0 0 80px;
  color: #888888;
  font-size: 14px;
  background: #1A1A1A;
}

.bottom-logo {
  display: block;
  margin-bottom: 32px;
}
.bottom-logo img {
  display: block;
}

.socials {
  display: flex;
  align-items: center;
}

.socials__link {
  margin-right: 19px;
}
.socials__link:last-child {
  margin-right: 0;
}

.page-footer__wrap {
  display: flex;
  align-items: flex-start;
  padding-top: 80px;
}

.page-footer__l {
  margin-right: 40px;
}

.page-footer__info {
  flex: 1;
}

.page-footer__warning {
  line-height: 1.45;
}
.page-footer__warning p {
  margin: 0 0 20px 0;
}
.page-footer__warning p:last-child {
  margin-bottom: 0;
}

.footnote a {
  color: #b3b3b3;
}
.footnote a:hover {
  text-decoration: none;
}

@media screen and (max-width: 991px) {
  h1 {
    font-size: 48px;
  }
  h3 {
    font-size: 24px;
  }
}
@media screen and (max-width: 767px) {
  body {
    font-size: 14px;
  }
  h1 {
    font-size: 32px;
    margin-bottom: 20px;
  }
  h2 {
    font-size: 24px;
    margin-bottom: 30px;
  }
  h3 {
    font-size: 20px;
    margin-bottom: 20px;
  }
  h4 {
    font-size: 18px;
  }
  .btn {
    padding: 12.5px 20px;
    font-size: 18px;
  }
  .page-header__info {
    display: none;
  }
  .page-footer {
    padding: 20px 0;
    font-size: 12px;
  }
  .page-footer p {
    margin: 0 0 15px 0;
  }
  .page-footer p:last-child {
    margin-bottom: 0;
  }
  .page-top {
    padding: 40px 0;
  }
  .page-footer {
    padding: 0 0 40px;
    font-size: 12px;
  }
  .page-footer__wrap {
    flex-wrap: wrap;
    padding-top: 40px;
    padding-bottom: 45px;
    position: relative;
  }
  .page-footer__l {
    margin-right: 0;
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
  }
  .page-footer__info {
    text-align: center;
  }
}
@media screen and (max-width: 600px) {
  input[type=text]:-webkit-autofill {
    font-size: 12px !important;
  }
  input[type=text]:-webkit-autofill,
  input[type=text]:-webkit-autofill:hover,
  input[type=text]:-webkit-autofill:focus,
  input[type=text]:-webkit-autofill:active {
    font-size: 12px !important;
  }
  input:-webkit-autofill::first-line {
    font-size: 12px !important;
  }
  .p-disclaimer {
    font-size: 10px;
    line-height: 12px;
  }
  .page-header__wrap {
    justify-content: center;
    padding: 5px 0;
  }
}