.page-footer {
    padding: 0 0 80px;
    color: #888888;
    font-size: 14px;
    background: #1A1A1A;
}

.bottom-logo {
    display: block;
    margin-bottom: 32px;
    img {
        display: block;
    }
}

.socials {
    display: flex;
    align-items: center;
}

.socials__link {
    margin-right: 19px;
    &:last-child {
        margin-right: 0;
    }
}

.page-footer__wrap {
    display: flex;
    align-items: flex-start;
    padding-top: 80px;
}

.page-footer__l {
    margin-right: 40px;
}

.page-footer__info {
    flex: 1;
}

.page-footer__warning {
    line-height: 1.45;
    p{
        margin: 0 0 20px 0;
        &:last-child{
            margin-bottom: 0;
        }
    }
}
.footnote{
    a {
        color: #b3b3b3;
        &:hover{
            text-decoration: none;
        }
    }
}